const formatCurrency = (value?: number) => {
    if (typeof value !== 'number') return '-';
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value);
};

const formatMonthLabel = (monthKey?: string) => {
    if (!monthKey) return '‚Äî';
    const [year, month] = monthKey.split('-');
    return `Th√°ng ${month}/${year}`;
};

const formatDateDisplay = (dateString?: string) => {
    if (!dateString) return '-';
    return formatDateCustom(new Date(dateString), "dd/MM/yyyy");
};

/**
 * Format EmployeeAddress th√†nh chu·ªói hi·ªÉn th·ªã
 * - 3-c·∫•p: "123 ABC, Qu·∫≠n 7, Ph∆∞·ªùng T√¢n Ph√∫, TP.HCM" (ƒë·∫ßy ƒë·ªß District + Ward)
 * - 2-c·∫•p: "123 ABC, Ph∆∞·ªùng T√¢n Ph√∫, TP.HCM" (ch·ªâ Ward, b·ªè District)
 */
const formatAddressDisplay = (addr: EmployeeAddress | null | undefined): string => {
  if (!addr) return '-';
  
  const { street, ward, district, province, inputLevel } = addr;
  
  if (inputLevel === '3-level') {
    // 3-c·∫•p: Hi·ªÉn th·ªã District + Ward
    return [street, district, ward, province].filter(Boolean).join(', ') || '-';
  } else {
    // 2-c·∫•p: Ch·ªâ hi·ªÉn th·ªã Ward (b·ªè District)
    return [street, ward, province].filter(Boolean).join(', ') || '-';
  }
};

